.white-bg{
  opacity: 0;
  transition: all 0.3s 
}
.white-bg:hover{
  background-color: rgba(255, 255, 255, 0.822);
  opacity: 1;
  transition: all 0.3s 
}

.burger-nav{
  position: fixed;
  width: 3rem;
  height: 3rem;
  top: 1rem;
  right: 1rem;
  background: rgb(179, 178, 178);
  
  opacity: 0;
  transition: 0.5s
}
.burger-nav.burger-show{
  opacity: 1;
  z-index: 1070;
  cursor: pointer;
}
.icon{
  width: 2rem;
  height: 0.3rem;
  background: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 2px 5px rgba(0,0,0,.2);
  z-index: 1070;
  transition: .5s;
  
}
.icon::before,
.icon::after{
 content: '';
 width: 2rem;
  height: 0.3rem;
  background: #ffffff;
  position: absolute;
  box-shadow: 0 2px 5px rgba(0,0,0,.2);
  transition: .5s;
  
}
.icon::before{
  top: -10px
}
.icon::after{
  top: 10px
}
.icon.active{
  background: transparent;
  box-shadow: 0 2px 5px rgba(0,0,0,0);
}
.icon.active::before{
  top:0;
  transform: rotate(45deg)
}

.icon.active::after{
  top:0;
  transform: rotate(135deg)
}
.burger-nav-active{
  position: none;
  list-style-type: none;
  background: rgb(179, 178, 178);
  height: 1rem;
  width: 2rem;
  opacity: 0;
  transition: 0.5s;
  z-index: 10;
  pointer-events: none
}
.burger-link{
  color: white;
  transition: 0.5s
}

.burger-link:hover{
  color: white;
  transform: scale(1.1)
}

.burger-nav-active.show{
  position: absolute;
  opacity: 1;
  top: 0px;
  right: 45px;
  height: 3rem;
  width: 22rem;
  z-index: 1000;
  pointer-events: all
}

@media (max-width: 420px){
  .burger-nav{
    top: 0.25rem;
    right: 0.25rem;
  }
  .burger-nav-active.show{
    height: 99vh;
    width: 98vw; 
    right: 0;
  }
}